export function getWhatsAppLink(message: string): string {
  const phoneNumber = "5511984349149" // +55 11 98434-9149
  const encodedMessage = encodeURIComponent(message)
  return `https://wa.me/${phoneNumber}?text=${encodedMessage}`
}

